
.btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  border: none;
  padding: 15px 20px;

  cursor: pointer;

  border-radius: 25px;

  font-size: 28px;
  font-style: italic;
  margin-left: 20px;
}

.btn_one:hover,
.btn_one:focus {
  background-color: rgb(50, 50, 206);
}

.btn_two:hover,
.btn_two:focus {
  background-color: rgb(197, 16, 16);
}